// Colors
// ---------------------------------

// Default

$color-default-text:                #2d383f;
$color-default-text-antagonist:     #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);
$color-default-white:               #ffffff;
$color-default-black:               #000000;

// bg
$color-default-bg:                  #ffffff;

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #0266c1;
$color-torch-red:                   #ff1553;
$color-navy-blue:                   #1872da;
$color-resolution-blue:             #1c3374;
$color-polo-blue:                   #89a6ca;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-default:                    "Montserrat", "Arial", sans-serif;
$ff-gotham-pro-light:           "Gotham Pro Light", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

$trans-default-time:                200ms;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-400:                            400px;
$vp-768:                            768px;
$vp-800:                            800px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;
$vp-2000:                           2000px;
$vp-2500:                           2500px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;

// container padding
$desktop-padding:                    5px;
$tablet-padding:                     45px;
$mobile-padding:                     15px;

// header
$header-height-desktop:              102px;
$header-height-tablet:               74px;
$header-height-mobile:               69px;
